@import '../../global.scss';

.portfolio {
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    
    @include mobile {
        padding: 0 20px !important;
        h1 {
            margin-top: 200px !important;
        }


        ul {
            justify-content: center;
            align-items: center;
            text-align: center !important;
            li {
                text-align: center;
                font-size: 12px !important;
                margin-right: 20px !important;
                padding: 20px !important;
            }
        }
        
    }

    h1 {
        margin-top: 400px;
        font-size: 50px;
    }

    ul {
        margin: 10px;
        padding: 0;
        list-style: none;
        display: flex;

        li {
            font-size: 14px;
            font-weight: 700;
            margin-right: 50px;
            padding: 7px;
            cursor: pointer;
            border-radius: 10px;

            &.active {
                background-color: $mainCollor;
                color: white;
            }
        }
    }

    .text {
        width: 50%;
        margin-top: 60px ;
        text-align: center;
        font-size: 10px;
        font-weight: 500;

        h4 {
            font-size: 17px;
            font-weight: 700;
            margin: 10px 0;
        }
    }

    .container {
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        overflow: hidden;

        .item {
            width: 220px;
            height: 150px;
            border-radius: 20px;
            border: 1px solid skyblue;
            margin: 10px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
            position: relative;
            transition: all .5 ease;
            cursor: pointer;
            overflow: hidden;


            h3 {
                position: absolute;
                font-size: 20px;
                display: none;
            }

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                z-index: 1;
                overflow: hidden;
            }

            &:hover {
                background-color: $mainCollor;
                h3 {
                    display: block;
                }

                a {
                    
                    img {
                        opacity: 0.2;
                        z-index: 0;
                    }
                }
            }
        }
    }
}